.block-progress {
  font-family: $CenturySchlbkCyrillicBT;
  &--size {
    max-width: 1000px;
    margin: 0 auto;
  }
  &__item {
    display: flex;
    flex-direction: column;
    height: 100%;
    color: #333333;

    @include rem(padding, 20px 0);
  }
  &__val {
    @include rem(padding-bottom, 40px);
    @include rem(margin-bottom, 20px);
    @include ff(
      'font-size',
      (
        768px: 30px,
        1280px: 35px,
        1800px: 42px,
      )
    );
     font-family: $CenturyGothic-Bold;

    border-bottom: 1px solid $gray-300;
  }
  &__text {
    @include ff(
      'font-size',
      (
        768px: 16px,
        1280px: 18px,
        1800px: 21px,
      )
    );
  }

  &__icon {
    fill: #fc0;
  }
}
