.social {
  &__list {
    @include listReset();
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    &:not(:last-child) {
      @include rem(margin-right, 20px);
    }
  }
  &__icon {
    fill: #b3b3b3;

  }
  &__link {
    &:hover {
      & .social__icon {
        fill: $color-base;
      }
    }
  }
}
