.contacts {
  &__list {
    @include listReset();
    font-family: $CenturySchlbkCyrillicBT;
  }
  &__item {
    @include rem(margin-bottom, 10px);
    flex-wrap: wrap;

    @include media-breakpoint-up(sm) {
      flex-wrap: nowrap;
    }
  }
  & a {
    color: $text-color;

  }
  & a[href^="mailto:"] {
    color: $color-base;
  }
  &__icon {
    fill: #b3b3b3;

    @include rem(
      (
        width: 20px,
        height: 20px,
        margin-right: 20px
      )
    );
  }
}
