.partners {
  &__item {
    text-align: left;
  }
  &__name {
    font-family: $CenturyGothic-Bold;

    @include rem(
      (
        font-size: 16px,
        margin-top: 10px,
        margin-bottom: 10px
      )
    );
  }
  &__text {
    @include rem(
      (
        font-size: 14px,
        margin-bottom: 10px
      )
    );
  }
}
