.block-projects {
  // padding: 3rem 0;
  // background-color: #f7f7f8;
  position: relative;

  & .container {
    position: relative;
    max-width: 1000px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
  }
  & .row {
    // width: 100%;
  }

  & .nav-tabs {
    position: relative;

    @include media-breakpoint-up(md) {
      position: absolute;
    top: 0;
    right: 15px;
    }
  }

  & .btn {
    text-align: center;
    font-family: $CenturyGothic-Bold;
    text-transform: uppercase;
  }

  &__title {
    margin-right: auto;
  }

  &__header {
    font-family: $CenturySchlbkCyrillicBT;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include rem(
      (
        min-height: 350px,
        margin-bottom: 20px,
        padding: 20px
      )
    );
    background-size: cover;
    background-repeat: no-repeat;
    background-color: $bg-color;

    transition: 1s;

    color: #ffffff;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
    }
  }
  &__item-header {
    position: relative;
    z-index: 2;
  }
  &__item-bottom {
    position: relative;
    z-index: 2;
  }
  &__place {
    display: flex;
    flex-direction: column;
  }
  &__place-name {
    font-family: $CenturyGothic-Bold;
    @include rem(margin-bottom, 10px);
  }
  &__place-pin {
    @include rem(margin-bottom, 15px);
  }
  &__number {
    display: flex;
    align-items: center;
    & span {
      font-family: $CenturyGothic-Bold;
      @include ff(
        "font-size",
        (
          768px: 40px,
          1280px: 45px,
          1800px: 50px
        )
      );
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include rem(margin-bottom, 40px);
  }

  &__icon {
    fill: #ffffff;
  }
}
