.page-footer {
  @include rem(padding, 60px 0);
  font-family: $CenturySchlbkCyrillicBT;
  background-color: #4d4d4d;
  color: #ffffff;

  & .container {
    max-width: 1920px;
    margin: 0 auto;
  }
  &__item {
    display: flex;
    flex-direction: column;


    height: 100%;

    @include media-breakpoint-up(lg) {
      align-items: center;
    }
  }

  &__field {
    align-self: flex-start;
    display: block;
    color: #ffffff;
    text-decoration: none;

    @include media-breakpoint-up(xl) {
      align-self: center;
    }

    &:focus,
    &:hover {
      text-decoration: none;
      color: #ffffff;
    }
  }
  & .contacts a {
    color: #ffffff;
  }
  & .contacts a[href^="mailto:"] {
    color: #ffffff;
  }
}
