.page-header {
  // display: none;
  // position: relative;
  // z-index: 2;
  // top: 0;
  // left: 50%;
  width: 100%;
  min-height: 60px;

  flex-wrap: wrap;
  justify-content: space-between;

  @include rem(
    (
      margin-bottom: 30px,
      padding-top: 10px,
      padding-bottom: 20px
    )
  );

  // transform: translate(-50%, 0);

  &--bg {
    background-color: $color-base;
  }

  &--hidden {
    transform: translate3d(0, -90px, 0);
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    max-width: 1920px;
  }

  &__logo {
    width: 65%;

    @include media-breakpoint-up(md) {
      width: 100%;
    }
  }

  &__right {
    display: none;

    @include media-breakpoint-up(lg) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
    }

    & .block-search {
      margin-right: 30px;
    }
  }

  &__link {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    color: #ffffff;
    text-decoration: none;

    &:first-child {
      margin-right: 10px;
    }
  }

  &__icon {
    margin-right: 5px;
    fill: #ffffff;
  }
}
