.employee {
  display: flex;
  flex-direction: column;
  @include rem(margin-bottom, 40px);

  &__top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    @include rem(margin-bottom, 20px);
  }

  &__pic {
    @include rem(margin-right, 20px);
  }

  &__name {
    @include rem(max-width, 250px);
    font-family: $CenturyGothic-Bold;
    color: $text-color;
    @include ff(
      "font-size",
      (
        768px: 18px,
        1280px: 20px,
        1800px: 25px
      )
    );
  }

  &__bottom {
    @include ff(
      "font-size",
      (
        768px: 16px,
        1280px: 18px,
        1800px: 20px
      )
    );
  }

}
