.slider {
  // min-height: calc(100vh - 170px);
  min-height: 70vh;
  margin-bottom: -1px;
  & .swiper-container {
    width: 100%;
    height: 100%;
    // min-height: calc(100vh - 170px);
    min-height: 70vh;
  }
  & .swiper-wrapper {
    width: 100%;
    height: 100%;
    // min-height: calc(100vh - 170px);
    min-height: 70vh;
  }
  & .swiper-slide {
    // min-height: calc(100vh - 170px);
    min-height: 70vh;
    text-align: center;
    font-size: 18px;
    background: #ddd;
    background-size: cover;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }

  & .swiper-pagination-bullet {
    background-color: transparent;
    border: 2px solid #ffffff;
    width: 10px;
    height: 10px;
  }
  & .swiper-pagination-bullet-active {
    background-color: #ffffff;
  }
}
