.breadcrumb {
  @include listReset();
  display: flex;
  flex-wrap: wrap;
  margin-right: auto;
  font-family: $CenturySchlbkCyrillicBT;
  &__item {
    position: relative;
    color: #999999;
    font-size: 16px;
    &:not(:last-child) {
      @include rem(padding-right, 15px);
    }
    &:not(:last-child)::before {
      content: "/";
      position: absolute;
      top: 0;
      right: 5px;
      width: 5px;
      height: auto;
    }
    & a {
      color: #999999;
    }
  }
  &.active {
    color: $gray-500;
  }
}
