.block-lang {
  @include media-breakpoint-up(lg) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__link {
    display: block;
    @include rem(padding, 10px);
    line-height: normal;
    text-decoration: none;

    line-height: normal;
    font-weight: bold;

    @include ff(
      'font-size',
      (
        767px: 13px,
        768px: 10px,
        1280px: 15px,
        1800px: 17px,
      )
    );
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.06em;
    color: #ffffff;
    text-transform: uppercase;

    &:focus,
    &:hover {
      text-decoration: none;
      color: #ffffff;
    }
  }
}
