.advantages {
  width: 100%;
  &--white {
    color: #ffffff;
  }
  &__item {
    min-height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  &__item-title {
    display: flex;
    flex-direction: column;
  }
  &__item-name {
    font-family: $CenturyGothic-Bold;

    @include rem(margin-bottom, 10px);
  }
  &__item-text {
    @include rem(max-width, 250px);
  }
  &__icon {
    fill: #ffffff;

    @include rem(
      (
        width: 60px,
        height: 60px,
        margin-bottom: 15px
      )
    );
  }
}

.advantages--white {
  & .advantages__item-text {
    @include rem(max-width, 300px);
    font-weight: 600;
  }

}
