.article {
  &__list {
    @include listReset();
    display: flex;
    flex-wrap: wrap;
    color: #808080;
  }
  &__list-item {
    position: relative;
    display: flex;
    align-items: center;
    &:not(:last-child) {
      @include rem(margin-right, 20px);
    }

    &::before {
      content: '';
    }
  }
  &__list-link {
    display: flex;
    align-items: center;
    flex-grow: 1;

    @include rem(padding-left, 10px);
    color: #808080;
    text-decoration: underline;

    &:focus,
    &:hover {
      color: $color-base;
    }
  }
  &__list-icon {
    margin-right: 10px;
  }
  &__list-content {
    @include listReset();
    @include rem(padding-left, 20px);

    & li {
      position: relative;

      @include rem(
        (
          padding-left: 10px,
          margin-bottom: 20px,
        )
      );

      &::before {
        position: absolute;
        content: '\2022';
        color: $list-marker-color;
        font-size: 20px;
        @include rem(left, -10px);
      }
    }
  }
  &__icon {
    fill: #b3b3b3;
  }
  &__marker {
    font-family: $CenturyGothic-Bold;
  }
}
