.documents {
  &__list {
    @include listReset();
  }
  &__list-item {
    display: flex;
    @include rem(margin-bottom, 20px);
  }
  &__list-icon {
    @include rem(width, 20px);
  }
  &__list-link {
    width: 80%;
    color: $text-color;

    &:focus,
    &:hover {
      color: $color-base;
      text-decoration: none;
    }
  }
}
