.nav-tabs {
  @include listReset();
  display: flex;
  align-self: flex-start;
  flex-wrap: wrap;
}

.nav-item {
  @include rem(margin-bottom, 10px);
  &:not(:last-child) {
    @include rem(margin-right, 30px);
  }
}

.nav-link {
  position: relative;
  font-family: $CenturyGothic-Bold;
  color: $color-base;
  font-weight: 600;
  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: $color-base;
  }
  &:focus,
  &:hover {
    text-decoration: none;
    color: $color-base;
  }
}

.nav-link.active {
  color: $text-color;
  &::before {
    background-color: transparent;
  }
}

.tab-pane {
  display: none;
}

.tab-pane.active {
  display: block;
}
