.promo {
  &__slider {
    background-color: $color-base;
    &.col-12 {
      padding: 0;
    }
    & .swiper-slide {
      opacity: 0 !important;
    }

    & .swiper-slide.swiper-slide-active {
      opacity: 1 !important;
    }
  }

  &__content {
    // max-width: 1000px;
    // max-width: 630px

    @include rem(
      (
        padding-top: 100px,
        padding-bottom: 50px
      )
    );
    text-align: left;
    color: #ffffff;
  }
  &__title {
    @include rem(margin, 30px 0);
    font-weight: 600;
    line-height: normal;
    @include ff(
      "font-size",
      (
        768px: 25px,
        1280px: 35px,
        1800px: 40px
      )
    );
  }
  &__text {
    @include rem(margin, 30px 0);
    line-height: normal;
    font-weight: 600;
    @include ff(
      "font-size",
      (
        768px: 16px,
        1280px: 20px,
        1800px: 25px
      )
    );
  }
}
